#spotlight_wrapper {
  position: fixed;
  top: 0;
  height: 100%;
  left: 0;
  right: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.3);
  display: none;
  z-index: 999999999;
  padding-top: 10vh; }

.spotlightContainer {
  width: 70vw;
  position: relative;
  margin: auto;
  -webkit-box-shadow: 0 25px 60px 10px rgba(0, 0, 0, 0.3);
          box-shadow: 0 25px 60px 10px rgba(0, 0, 0, 0.3);
  border-radius: 5px 0 5px 5px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  background-color: #f8f5f4;
  overflow: hidden;
  opacity: 1;
  -webkit-animation: slideEffect 0.25s;
          animation: slideEffect 0.25s; }
  .spotlightContainer .label {
    font-size: 0.7rem;
    text-transform: uppercase;
    text-align: left;
    margin-top: 5px; }

@-webkit-keyframes slideEffect {
  from {
    -webkit-transform: scale(0);
            transform: scale(0); }
  to {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes slideEffect {
  from {
    -webkit-transform: scale(0);
            transform: scale(0); }
  to {
    -webkit-transform: scale(1);
            transform: scale(1); } }

#spotlightInput {
  display: block;
  width: 100%;
  height: 56px;
  margin: auto;
  outline: none;
  font-size: 1.6rem;
  color: #000;
  background-color: #ffffff;
  background-image: url(data:image/svg+xml;base64,);
  background-position: left 17px center;
  background-repeat: no-repeat;
  background-size: 5%;
  padding-left: 60px;
  -webkit-appearance: none;
  border: 1px solid #e0dcdb;
  border-radius: 0;
  border-width: 0 1px; }
  #spotlightInput::-webkit-input-placeholder {
    color: #acacac;
    font-size: 1.2rem; }
  #spotlightInput::-ms-input-placeholder {
    color: #acacac;
    font-size: 1.2rem; }
  #spotlightInput::placeholder {
    color: #acacac;
    font-size: 1.2rem; }

#spotlightResultArea {
  background-color: #f9f5f4;
  width: 100%;
  max-height: 350px;
  overflow: auto; }
  #spotlightResultArea .ReactTable {
    max-height: 349px !important; }

.helpText {
  height: 35px;
  width: 70vw;
  margin: 0 auto;
  text-align: right;
  cursor: pointer; }
  .helpText .helpTextEsc {
    font-size: 0.7rem;
    color: #000000;
    margin-right: 5px;
    padding: 1px 8px 3px;
    background: #e3feff;
    border-radius: 10px;
    border: 1px solid #02bbae; }
  .helpText .globalSearch-Close {
    background: #f7f5f4;
    border-radius: 5px 5px 0 0;
    color: #8c8c8c;
    font-size: 1.2rem;
    padding: 6px 15px;
    text-transform: uppercase;
    border: 1px solid #c8c6c5;
    line-height: 35px;
    text-shadow: 0 1px 1px #ffffff;
    cursor: pointer; }

.filterBySelect,
.searchBySelect {
  border-radius: 0;
  width: 100%;
  border: 1px solid #8c8c8c;
  padding: 3px 5px;
  font-size: 0.8rem;
  border-radius: 5px; }

.speckCntr {
  padding-left: 0;
  padding-right: 0; }
  .speckCntr .fa-microphone {
    font-size: 1.5rem;
    text-align: left;
    line-height: 56px;
    color: #8c8c8c; }
  .speckCntr .flash {
    -webkit-animation: colorchange 1s infinite alternate;
    text-shadow: 0 0 10px #ff0200; }

@-webkit-keyframes colorchange {
  0% {
    color: 8c8c8c; }
  50% {
    color: red; }
  100% {
    color: #8c8c8c; } }



